@use 'variables' as *;

.ai-tools-tooltip {
  max-width: 400px;
  min-height: 200px;
  position: absolute;
  display: none;
  z-index: 10;
  

  &.is-visible {
    display: block;
  }

  .tooltip-arrow {
    width: 24px;
    height: 24px;
    position: absolute;
    left: 316px;
    top: -6px;
    z-index: 2;
    color: var(--bg-default);

    svg {
      width: 100%;
      height: 100%;
      fill: var(--bg-default);
    }
  }
  .tooltip-arrow{
    top: -20px;
  }

  .tooltip-content {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    position: absolute;
    left: 0;
    top: 12px;
    height: 429px;
    overflow: hidden;
  }
  .ai-knowledge-popup{
    width: 400px;
    box-shadow: 0px -4px 18px 3px rgba(0, 0, 0, 0.2);
  }

  .tooltip-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

  &.is-above {
    .tooltip-arrow {
      top: auto;
      bottom: -20px;
      transform: rotate(180deg);
    }

    .tooltip-content {
      top: 0;
      bottom: auto;
    }
    .ai-knowledge-popup{
      box-shadow: 0px 20px 20px 0px rgba(0, 0, 0, 0.2);
    }
  }
}
